*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    font-family: 'Mulish',sans-serif;
}

.main{
    display: grid;
    grid-template-columns: 50px repeat(10,1fr) 50px;
    grid-template-rows: repeat(4, 1fr);
    width: 100%;
    height: 74vh;
    margin-top: 70px;
    /* background-color: rgb(165, 165, 112); */
}

.left{
    display: grid;
    grid-column: 2/7;
    grid-row: 2/4;
    /* background-color: rgb(150, 166, 167); */
}

.right{
    display: grid;
    grid-column: 7/12;
    grid-row: 1/5;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    /* background-color: rgb(167, 150, 150); */
}

.left_inner{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.lef_inn_head{
    font-size: 30px;
    font-weight: 600;
    color: rgb(0, 0, 0);
}

strong{
    color: #3498db;
    font-size: 35px;
    font-weight: 600;
    margin-top: -25px;
}

.left_inner p{
    font-size: 18px;
    font-weight: 500;
}

.lefBtn{
    background-color: transparent;
    color: #3498db;
    border: 2px solid #3498db;
    padding: 12px 20px;
    border-radius: 50px;
    font-weight: bold;
    font-size: 15px;
    transition: all .5s ease-in-out;
}

.lefBtn:hover{
    background-color: #3498db;
    color: #fff;
}

.right{
    display: flex;
    justify-content: center;
    align-items: center;
}

.right .animaImg{
    width: 500px;
    height: 400px;
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down{
    0% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    100% {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
}

@keyframes up-down {
    0% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    100% {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
}

