*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.contact_main{
    width: 100%;
    height: 77.5vh;
    display: grid;
    margin-top: 50px;
    grid-template-columns: 50px 1fr 50px;
    grid-template-rows: 60px 1fr;
    /* background-color: rgb(6, 112, 102); */
}

.contact_main .contHead{
    grid-column: 2/3;
    grid-row: 1/2;
    text-align: center;
}
.contact_form{
    grid-row: 2/3;
    grid-column: 2/3;
    width: 35%;
    /* background-color: whitesmoke; */
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
}
.marginB{
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 20px;
}

.marginB label,input,textarea,button{
    outline: none;
    font-size: 17px;
    font-weight: 500;
}

input{
    border: 1px solid rgba(0,0,0, 0.3); 
    padding: 5px 8px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
}

.btn .sbBtn{
    width: 135px;
    padding: 8px 15px;
    text-align: center;
    border: 2px solid #0c7eca;
    border-radius: 4px;
    background-color: #0c7eca;
    color: #fff;
    font-weight: 550;
}

.btn .sbBtn:hover{
    color: #0c7eca;
    background-color: #fff;
    border: 2px solid #0c7eca;
}

@media  screen and (max-width: 840px) {
    .contact_main{
        width: 100%;
        display: grid;
        margin-top: 50px;
        grid-template-columns: 15px 1fr 15px;
        grid-template-rows: 40px 1fr;
        /* background-color: rgb(6, 112, 102); */
    }
    .contact_main{
        margin-top: 120px;
    }
    .contact_form{
        width: 80%;
    }
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    font-family: 'Mulish',sans-serif;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.service_main{
    margin-top: 5px;
    display: grid;
    grid-template-columns: 50px  1fr 50px;
    grid-template-rows: 40px auto;
    width: 100%;
    height: auto;
}
.service_main h1{
    text-align: center;
    grid-column: 2/3;
    grid-row: 1/2;
}

.container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    grid-column: 2/3;
    grid-row: 2/3;
}
.card_main{
    margin-top: 20px;
    width: 380px;
    height: 370px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    /* background-color: rgb(214, 184, 241); */
    grid-column: 2/3;
    grid-row: 2/3;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.card_img{
    width: 380px;
    height: 250px;
    box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
}

.card_img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    cursor: pointer;
    transition: all .6s ease-out;
}

.card_img img:hover{
    opacity: .9;
}

.card_content{
    display: flex;
    width: 100%;
    flex-direction: column;
    /* background-color: burlywood; */
    padding: 10px 15px;
}

.card_content p{
    font-size: 14px;
    letter-spacing: .4px;
}

.card_content a{
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    background-color: #0c7eca;
    width: 100px;
    text-align: center;
    padding: 7px;
    border-radius: 4px;
    margin-top: 8px;
    cursor: pointer;
    transition: all .5s ease-in-out;
    border: 2px solid #0c7eca;
}

.card_content a:hover{
    opacity: .8;
    background-color: transparent;
    border: 2px solid #0c7eca;
    color: #0c7eca;
}

